* {
    text-align: center;
}

/* book component */
.btn {
    background-image: linear-gradient(to right, #2b5876 0%, #4e4376 51%, #2b5876 100%);
    margin: 6px;
    padding: 5px 15px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;
    box-shadow: 0 0 20px #eee;
    border-radius: 10px;
}

.btn:hover {
    background-position: right center;
    color: #fff;
    text-decoration: none;
}

.image {
    width: 100%;
    height: 300px;
}

.lable {
    font-size: 20px;
    font-weight: bold;
}

input {
    width: 40%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
}

/* navbar */
.nav {
    background-color: #2b5876;
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: white;
}

.link {
    color: white;
    text-decoration: none;
    font-size: large;
}

/* about page */
.about{
    line-height: 3.5;
    font-size: 20px;
}

/* rating star component */
input[type="radio"] {
    display: none;
}

.star{
    cursor: pointer;
    transition: color 200ms;
}

/* contact */
.form-control{
    width: 40%;
    margin-bottom: 10px;
    background-color: blue;
}